

























































import {
  computed, defineComponent, onMounted, onUnmounted, PropType, ref,
} from '@vue/composition-api';
import { useWindowScrollSettings } from '@/app/shared/client/logic/scroll-settings';
import { useTileOffsetSettings } from '@/app/shared/client/logic/offset-settings';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';
import {
  StyleKeyValue,
  tileStyleSelector,
  colorStyles,
  generalStyles,
  textStyles,
  imageStyles,
  imageOverlayStyles,
  actionLinkStyles,
} from '@/app/website/client/logic/tile-style';
import { checkIfTileTransparent } from '@/app/website/client/logic/tile-properties';
import {
  ImageSet,
  getImageStyleSelector,
  getImageSet,
} from '@/app/website/client/logic/image-style';
import {
  CTATileContent,
  CTATileDesign,
  CTATileLayout,
} from '@/app/shared/client/type/tile/cta-tile';
import {
  ImageSettings,
} from '@/app/shared/client/type/settings/image-settings';
import {
  ActionLinkSettings,
} from '@/app/shared/client/type/settings/action-link-settings';
import { SidebarSettings } from '@/app/shared/client/type/settings/sidebar-settings';
import {
  GeneralDesignSettings,
} from '@/app/shared/client/type/settings/general-settings';
import { Breakpoints, ViewportSettings } from '@/app/shared/client/type/settings/viewport-settings';
import { TextSettings } from '@/app/shared/client/type/settings/text-settings';
import { checkIfTileShown } from '@/app/website/client/logic/appearance-behaviour';
import BannerCenterDefaults from '@/app/website/client/defaults/cta/banner-center.json';
import BannerLeftDefaults from '@/app/website/client/defaults/cta/banner-left.json';
import BannerRightDefaults from '@/app/website/client/defaults/cta/banner-right.json';
import BannerBottomDefaults from '@/app/website/client/defaults/cta/banner-bottom.json';
import PromoBarLeftDefaults from '@/app/website/client/defaults/cta/promo-bar-left.json';
import PromoBarRightDefaults from '@/app/website/client/defaults/cta/promo-bar-right.json';
import StoryLeftDefaults from '@/app/website/client/defaults/cta/story-left.json';
import StoryRightDefaults from '@/app/website/client/defaults/cta/story-right.json';
import FullwidthCenterDefaults from '@/app/website/client/defaults/cta/fullwidth-center.json';
import FullwidthLeftDefaults from '@/app/website/client/defaults/cta/fullwidth-left.json';
import { CTATileLayoutDefaults } from '@/app/shared/client/type/defaults/cta-tile-defaults';
import { State } from '@/app/website/client/store/state';
import { ImageData } from '@/app/shared/client/type/image';
import {
  getImageInUploadingState,
} from '@/app/website/client/logic/image-uploading';
import FontLoader from '@/app/website/client/view/component/FontLoader.vue';
import ActionLink from '@/app/website/client/view/component/ActionLink.vue';
import TileTitle from '@/app/website/client/view/component/TileTitle.vue';

interface CTATileProps {
  tileId: string;
  content: CTATileContent;
  design: CTATileDesign;
  viewportSettings: ViewportSettings;
}

function getDefaults(layout: CTATileLayout | undefined): CTATileLayoutDefaults | undefined {
  switch (layout) {
    case 'BANNER_CENTER':
      return BannerCenterDefaults as CTATileLayoutDefaults;
    case 'BANNER_LEFT':
      return BannerLeftDefaults as CTATileLayoutDefaults;
    case 'BANNER_RIGHT':
      return BannerRightDefaults as CTATileLayoutDefaults;
    case 'BANNER_BOTTOM':
      return BannerBottomDefaults as CTATileLayoutDefaults;
    case 'PROMO_BAR_LEFT':
      return PromoBarLeftDefaults as CTATileLayoutDefaults;
    case 'PROMO_BAR_RIGHT':
      return PromoBarRightDefaults as CTATileLayoutDefaults;
    case 'STORY_LEFT':
      return StoryLeftDefaults as CTATileLayoutDefaults;
    case 'STORY_RIGHT':
      return StoryRightDefaults as CTATileLayoutDefaults;
    case 'FULLWIDTH_CENTER':
      return FullwidthCenterDefaults as CTATileLayoutDefaults;
    case 'FULLWIDTH_LEFT':
      return FullwidthLeftDefaults as CTATileLayoutDefaults;
    default:
      return undefined;
  }
}

function computedLayoutClass(layout: CTATileLayout | undefined): string {
  switch (layout) {
    case 'BANNER_CENTER':
      return 'banner-center';
    case 'BANNER_RIGHT':
      return 'banner-right';
    case 'BANNER_BOTTOM':
      return 'banner-bottom';
    case 'BANNER_LEFT':
      return 'banner-left';
    case 'PROMO_BAR_LEFT':
      return 'promo-bar-left';
    case 'PROMO_BAR_RIGHT':
      return 'promo-bar-right';
    case 'STORY_RIGHT':
      return 'story-right';
    case 'STORY_LEFT':
      return 'story-left';
    case 'FULLWIDTH_CENTER':
      return 'fullwidth-center';
    case 'FULLWIDTH_LEFT':
      return 'fullwidth-left';
    default:
      return 'banner-left';
  }
}

function customStyles(sidebar?: SidebarSettings): StyleKeyValue[] {
  let styles: StyleKeyValue[] = [];
  if (sidebar?.color !== undefined) {
    styles = [
      ...styles,
      ...colorStyles('--sidebar-solid-color', sidebar.color),
    ];
  }

  return styles;
}

function tileStyles(
  design: CTATileDesign,
  imageData?: ImageData,
): StyleKeyValue[] {
  let sidebar;
  if (design.layout === 'PROMO_BAR_LEFT' || design.layout === 'PROMO_BAR_RIGHT') {
    sidebar = design?.sidebar;
  }
  const styles: StyleKeyValue[] = [
    ...generalStyles(design.general),
    ...textStyles('title', design.title),
    ...textStyles('description', design.description),
    ...imageOverlayStyles('image', design.image),
    ...actionLinkStyles('button', design.actionLink),
    ...imageStyles('image', imageData),
    ...customStyles(sidebar),
  ];

  return styles;
}

const CTATile = defineComponent({
  components: {
    FontLoader,
    ActionLink,
    TileTitle,
  },
  props: {
    tileId: {
      required: true,
      type: String,
    },
    content: {
      required: true,
      type: Object as PropType<CTATileContent>,
    },
    design: {
      required: true,
      type: Object as PropType<CTATileDesign>,
    },
    viewportSettings: {
      required: true,
      type: Object as PropType<ViewportSettings>,
    },
    isHeadTile: {
      required: true,
      type: Boolean,
    },
  },
  setup(props: CTATileProps, { attrs, root, emit }) {
    const tileRef = ref<HTMLElement>();

    const {
      windowScrollTop,
      subscribeOnWindowScrollChange,
    } = useWindowScrollSettings();

    const {
      tileOffsetTop,
      tileOffsetHeight,
      subscribeOnTileOffsetChange,
    } = useTileOffsetSettings(tileRef);

    const layoutClass = computed(() => `ins-tile--${computedLayoutClass(props.design.layout)}`);
    const defaults = computed(() => getDefaults(props.design.layout));

    const design = computed<CTATileDesign>(() => {
      const userValue = props.design;
      const general: GeneralDesignSettings = {
        ...defaults.value?.general,
        ...userValue.general,
        background: {
          ...defaults.value?.general?.background,
          ...userValue.general?.background,
          solid: {
            ...defaults.value?.general?.background?.solid,
            ...userValue.general?.background?.solid,
          },
          gradient: {
            ...defaults.value?.general?.background?.gradient,
            ...userValue.general?.background?.gradient,
          },
        },
      };
      const title: TextSettings = {
        ...(defaults.value?.title as TextSettings),
        ...userValue.title,
      };
      const description: TextSettings = {
        ...(defaults.value?.description as TextSettings),
        ...userValue.description,
      };
      const actionLink: ActionLinkSettings = {
        ...(defaults.value?.actionLink as ActionLinkSettings),
        ...userValue.actionLink,
      };
      const image: ImageSettings = {
        ...defaults.value?.image,
        ...userValue.image,
        overlay: {
          ...defaults.value?.image?.overlay,
          ...userValue.image?.overlay,
          solid: {
            ...defaults.value?.image?.overlay?.solid,
            ...userValue.image?.overlay?.solid,
          },
          gradient: {
            ...defaults.value?.image?.overlay?.gradient,
            ...userValue.image?.overlay?.gradient,
          },
        },
      };
      const sidebar: SidebarSettings = {
        ...(defaults.value?.sidebar as SidebarSettings),
        ...userValue.sidebar,
      };

      return {
        layout: userValue.layout,
        general,
        title,
        description,
        actionLink,
        image,
        sidebar,
      };
    });

    const isTileShowRef = ref(false);
    const isTileShown = computed(() => checkIfTileShown(
      isTileShowRef,
      props.viewportSettings,
      windowScrollTop.value,
      tileOffsetTop.value,
      tileOffsetHeight.value,
    ));

    const fonts = computed(() => [
      design.value.title?.font,
      design.value.description?.font,
      design.value.actionLink?.font,
    ]);

    const styleSelector = computed(() => {
      const styles = tileStyles(
        design.value,
        props.content.image,
      );
      return tileStyleSelector(`#${attrs.id}`, styles);
    });

    const hasTitle = computed(() => {
      const isTitleVisible = design.value.title?.visible;
      const hasTitleContent = isNotNullOrEmpty(props.content.title);
      return isTitleVisible && hasTitleContent;
    });
    const hasDescription = computed(() => {
      const isDescriptionVisible = design.value.description?.visible;
      const hasDescriptionContent = isNotNullOrEmpty(props.content.description);
      return isDescriptionVisible && hasDescriptionContent;
    });
    const hasImage = computed(() => {
      const isImageVisible = design.value.image?.visible;
      const hasImageContent = props.content.image !== undefined;
      return isImageVisible && hasImageContent;
    });
    const hasTransparentBackground = computed(() => checkIfTileTransparent(
      design.value?.general,
      root.$store.state.siteSettings?.design,
    ));

    const uploadingImage = computed(() => {
      const { imageListInUploadingState } = root.$store.state as State;
      return getImageInUploadingState(imageListInUploadingState, props.tileId, 'image');
    });
    const isUploadingImage = computed(() => uploadingImage.value !== undefined);

    const actionLinkContent = computed(() => props.content.actionLink);
    const actionLinkDesign = computed(() => design.value.actionLink);
    const isPreview = root.$store.getters.isPreviewMode;

    const imageStyleSelector = computed(() => {
      const imageSet: ImageSet | undefined = getImageSet(
        root.$store.state.context,
        uploadingImage.value,
        props.content.image,
      );

      const styles: string[] = [];
      styles.push(getImageStyleSelector(
        `#${attrs.id}.ins-tile--banner-center .ins-tile__picture,
         #${attrs.id}.ins-tile--banner-right .ins-tile__picture,
         #${attrs.id}.ins-tile--banner-bottom .ins-tile__picture,
         #${attrs.id}.ins-tile--banner-left .ins-tile__picture,
         #${attrs.id}.ins-tile--fullwidth-center .ins-tile__picture,
         #${attrs.id}.ins-tile--fullwidth-left .ins-tile__picture`,
        {
          thumbnail: undefined,
          image1x: imageSet?.urlCropped1000,
          image2x: imageSet?.urlCropped1000,
          backgroundColor: imageSet?.backgroundColor,
        },
      ));

      styles.push(getImageStyleSelector(
        `#${attrs.id}.ins-tile--banner-center .ins-tile__picture,
         #${attrs.id}.ins-tile--banner-right .ins-tile__picture,
         #${attrs.id}.ins-tile--banner-bottom .ins-tile__picture,
         #${attrs.id}.ins-tile--banner-left .ins-tile__picture,
         #${attrs.id}.ins-tile--fullwidth-center .ins-tile__picture,
         #${attrs.id}.ins-tile--fullwidth-left .ins-tile__picture`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url1200,
          image2x: imageSet?.url2000,
          backgroundColor: imageSet?.backgroundColor,
        },
        Breakpoints.FROM_S,
      ));

      styles.push(getImageStyleSelector(
        `#${attrs.id}.ins-tile--banner-center .ins-tile__picture,
         #${attrs.id}.ins-tile--banner-right .ins-tile__picture,
         #${attrs.id}.ins-tile--banner-bottom .ins-tile__picture,
         #${attrs.id}.ins-tile--banner-left .ins-tile__picture,
         #${attrs.id}.ins-tile--fullwidth-center .ins-tile__picture,
         #${attrs.id}.ins-tile--fullwidth-left .ins-tile__picture`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url2000,
          image2x: imageSet?.url2000,
          backgroundColor: imageSet?.backgroundColor,
        },
        Breakpoints.FROM_L,
      ));

      styles.push(getImageStyleSelector(
        `#${attrs.id}.ins-tile--fullwidth-center .ins-tile__picture,
         #${attrs.id}.ins-tile--fullwidth-left .ins-tile__picture`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url2000,
          image2x: imageSet?.url3200,
          backgroundColor: imageSet?.backgroundColor,
        },
        Breakpoints.FROM_XL,
      ));

      styles.push(getImageStyleSelector(
        `#${attrs.id}.ins-tile--promo-bar-left .ins-tile__picture,
         #${attrs.id}.ins-tile--promo-bar-right .ins-tile__picture,
         #${attrs.id}.ins-tile--story-right .ins-tile__picture,
         #${attrs.id}.ins-tile--story-left .ins-tile__picture`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url600,
          image2x: imageSet?.url1200,
          backgroundColor: imageSet?.backgroundColor,
        },
      ));

      styles.push(getImageStyleSelector(
        `#${attrs.id}.ins-tile--promo-bar-left .ins-tile__picture,
         #${attrs.id}.ins-tile--promo-bar-right .ins-tile__picture,
         #${attrs.id}.ins-tile--story-right .ins-tile__picture,
         #${attrs.id}.ins-tile--story-left .ins-tile__picture`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url1200,
          image2x: imageSet?.url2000,
          backgroundColor: imageSet?.backgroundColor,
        },
        Breakpoints.FROM_S,
      ));

      return styles.join('');
    });

    onMounted(() => {
      emit('tile-loaded');
      subscribeOnWindowScrollChange();
      subscribeOnTileOffsetChange();
    });

    onUnmounted(() => {
      emit('tile-unloaded');
    });

    return {
      tileRef,
      layoutClass,
      fonts,
      styleSelector,
      imageStyleSelector,
      hasTitle,
      hasDescription,
      hasImage,
      hasTransparentBackground,
      isUploadingImage,
      isTileShown,
      actionLinkContent,
      actionLinkDesign,
      isPreview,
    };
  },
});

export default CTATile;
